angular.module('league')
  .factory('Member', ['$resource',
  	function($resource) {
  		return $resource('/league/:leagueId/members/:memberId',{
        leagueId : '@leagueId',
        memberId : '@_id'
      },{
        update: {
          method: 'PUT'
        }
      }
    );
  }
]);
